import './StageCreation.scss'
import { memo, useState, useCallback, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector, useDispatch } from 'react-redux'
import SmoothCollapse from 'react-smooth-collapse'
import { useNavigate, useParams } from 'react-router-dom'

import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu'
import { ReactComponent as ArrowDownIcon } from '../../icons/arrow_down.svg'
import HeaderTournament from '../../reusableComponents/HeaderTournament/HeaderTournament'
import { ReactComponent as PlusWhiteIcon } from '../../icons/plus_white.svg'
import { ReactComponent as RemoveIcon } from '../../icons/remove.svg'
import ArrowLinkBlock from '../../reusableComponents/ArrowLinkBlock/ArrowLinkBlock'
import RoundedButton from '../../extendedComponents/RoundedButton/RoundedButton'
// import { ReactComponent as EditNoteIcon } from '../../icons/edit_note.svg'
import { selectTournament, selectPlayersList } from '../../redux/selectors'
import { useCreateStageMutation } from '../../redux/stageService/stageApiSlice'

// 
import {
	useGetStageQuery
} from '../../redux/stageService/stageApiSlice.js'

// 
import {
	setPlayersList
} from '../../redux/stageService/stageSlice.js'

// 
import {
	formNodes
} from '../StageForm/externalFunctions.js'

import { calculateNumberOfGroups, calculateNumberOfPlayers } from './externalFunctions'

function StageCreationComponent({tournamentName, tournamentStartDate, communityName, stagesCount, tournamentUid}) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	// const { players, doubles } = useSelector(selectTournament)
	// 
	const { type, doubles, players, stages_count } = useSelector(selectTournament)

	// 
	const dispatch = useDispatch()

	const [showMainMenu, setShowMainMenu] = useState(false)
	const [groupType, setGroupType] = useState(1)
	const [count, setCount] = useState()
	const [generateStage] = useCreateStageMutation()

	// 
	const playersList = useSelector(selectPlayersList)

	// 
	const { tournamentParams, stageNumber } = useParams()
	
	// 
	const { data: stage = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, { refetchOnMountOrArgChange: true }) // Stage data

	useEffect(() => {
		if (Object.keys(stage).length !== 0) {
			const { levels, rating_initial } = stage
			const formedNodes = formNodes(rating_initial, players || doubles, type)

			dispatch(setPlayersList(formedNodes))
		}
	}, [stage, stageNumber, type, doubles, players, dispatch])


	useEffect(() => {
		setCount(calculateNumberOfGroups(players?.length || doubles?.length))
	}, [players, doubles])

	const handleGroupType = useCallback((number) => {
		if (number === 1) {
			setGroupType(1)
			const groupsNumber = calculateNumberOfGroups(players?.length || doubles?.length)
			setCount(groupsNumber)
		} else if (number === 2) {
			setGroupType(2)
			const firstGroupPlayers = calculateNumberOfPlayers(players?.length || doubles?.length)
			setCount(firstGroupPlayers)
		}
	}, [players, doubles])

	const handleMinus = useCallback(() => {
		if (count < 1) {
			return
		} else {
			setCount(prev => --prev)
		}
	}, [count])

	const handlePlus = useCallback(() => {
		if (count === 32) {
			return
		} else {
			setCount(prev => ++prev)
		}
	}, [count])

	const handleCounter = useCallback(() => {
		if (count > 32) {
			setCount(32)
		}

		return count
	}, [count])

	const generateStageSubmit = (async (event) => {
		// event.preventDefault()

		let body = null
		
		if (groupType === 1) {
			body = {
				'generate_method': 'equal',
				'groups_count': count
			}
		} else if (groupType === 2) {
			body = {
				'generate_method': 'unequal',
				'group1_participants_count': count
			}
		}
		
		try {
			const response = await generateStage({ tournament_uid: tournamentUid, body }).unwrap()
		
			navigate(`/${tournamentUid}/structure/${response?.order_number}`)
			
			console.log(`/${tournamentUid}/structure/${response?.order_number}`)
		}
		catch (e) {
			console.log('authomatical creation in stageCreation catch error', e)
		}
	})

	// console.log(playersList)

	return (
		<div className="stage-creation">
			<HeaderTournament
				name={tournamentName}
				date={tournamentStartDate}
				community={communityName}
			/>

			<div className="stage-creation__wrapper">
				<div className="stage-creation__header">
					<p className="stage-creation__quantity">
						{ players?.length || doubles?.length || 0 }
					</p>

					<p className="stage-creation__stage-number">
						 { stagesCount + 1 }

						{/* <button className="stage-creation__stage-button">
							<EditNoteIcon className="edit-note__icon" />
						</button> */}
					</p>

					<ArrowDownIcon
						className={`arrow-down__icon ${showMainMenu ? 'arrow--up' : 'arrow--down'}`}
						onClick={() => setShowMainMenu(prev => !prev)}
					/>
				</div>

				<div className="stage-creation__list-participants">
					<SmoothCollapse expanded={showMainMenu}>
						{
							players?.map((value, index) => {
								return <ArrowLinkBlock borderBottom={true} key={index}>
									{ index + 1 } { value?.first_name } { value?.formedName } [7]
								</ArrowLinkBlock>
							})
						}
					</SmoothCollapse>
				</div>

				<div className="stage-creation__radio-container">
					<div className="stage-creation__radio-element">
						<input
							id="radio-1"
							type="radio"
							name="stageType"
							value="1"
							checked={groupType === 1 ? true : false}
 							onChange={() => handleGroupType(1)}
						/>

						<label
							htmlFor="radio-1"
							className="stage-creation__radio-lable"
						>
							{ t('') }
						</label>
					</div>

					<div className="stage-creation__radio-element">
						<input
							id="radio-2"
							type="radio"
							name="stageType"
							value="2"
							checked={groupType === 2 ? true : false}
 							onChange={() => handleGroupType(2)}
						/>

						<label
							htmlFor="radio-2"
							className="stage-creation__radio-lable"
						>
							{ t('') }
						</label>
					</div>
				</div>

				<div className="stage-creation__counter-groupe">
					<p className="counter-groupe__name">
						{
							groupType === 1 ?
								' '
								:
								'-   1 '
						}
					</p>

					<div className="counter-groupe__container">
						<button className="counter-groupe__counter-button" onClick={() => handleMinus()}>
							<RemoveIcon className="remove-icon" />
						</button>

						<p className="counter-groupe__counter">
							{ handleCounter(count) }
						</p>

						<button className="counter-groupe__counter-button" onClick={() => handlePlus()}>
							<PlusWhiteIcon className="plus-white__icon" />
						</button>
					</div>
				</div>

				<div className="stage-creation__button-container">
					<RoundedButton
						title=" "
						onClick={(e) => generateStageSubmit(e)}
					/>

					{/* <RoundedButton
						title="  "
						background="white"
					/> */}

					<button className="stage-creation__delete-button">
						 
					</button>
				</div>
			</div>

			<CombineTournamentMenu />
		</div>
	)
}

export default memo(StageCreationComponent)

























