import './DoubleButton.scss'
import { memo } from 'react'

import { ReactComponent as BackIcon } from '../../icons/back.svg'

function DoubleButton({ title, onClickSmall, onClickBig }) {
	return (
		<div className="double-button__wrapper">
			<button
				className="double-button__small"
				onClick={onClickSmall}
			>
				<BackIcon className="back-icon" />
			</button>

			<button
				className="double-button__big"
				onClick={onClickBig}
			>
				{ title }
			</button>
		</div>
	)
}

export default memo(DoubleButton)