import './TournamentStanding.scss'
import React, { useEffect, useState, memo } from 'react'
import { useParams } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'

import { useGetStageQuery } from '../../redux/stageService/stageApiSlice'
import { selectTournament } from '../../redux/selectors'
import { assignAlphabeticalNames, getParametrs } from '../../utils/functions'
import { GROUP_TYPE_RR } from '../../utils/constants'
import RoundRobinGrid from '../../extendedComponents/RoundRobinGrid/RoundRobinGrid'
import OlympicPlusGrid from '../../extendedComponents/OlympicPlusGrid/OlympicPlusGrid'
import NoContent from '../../reusableComponents/NoContent/NoContent'
import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu'

import { sortByRanking } from './externalFunctions'

const TournamentStandingComponent = ({ auth }) => {
	const { t } = useTranslation()
	const { tournamentParams, stageNumber } = useParams()
	const { tournamentUid } = getParametrs(tournamentParams)
	const { stages_count } = useSelector(selectTournament)

	const { data: stageData = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, { skip: !stages_count || stages_count < 1, refetchOnMountOrArgChange: stages_count > 0 }) // Stage data

	const [stage, setStage] = useState({})

	useEffect(() => {
		if (Object.keys(stageData).length !== 0) {
			const stageWithNamedGroups = assignAlphabeticalNames(stageData)
			setStage(stageWithNamedGroups)
		}
	}, [stageData])

	if (isLoading) { return <h1>Loading</h1> }

	function getGroup(group) {
		const { nodes, results } = sortByRanking(group)
		return { ...group, nodes, results }
	}

	return (
		<>
			<div className="standing__container">
				<div className="standing__wrapper">
					{
						stages_count && stages_count > 0 ?
							stage?.levels?.map((level, levelIndex) => {
								return (
									<React.Fragment key={levelIndex}>
										{
											stage?.levels.length > 1 &&
											<p className="standing__header">
												{t('Level') + ' ' + (levelIndex + 1)}
											</p>
										}

										{
											level?.groups?.map((grp, index) =>
												<div key={index}>
													{
														grp?.type === GROUP_TYPE_RR ?
															<RoundRobinGrid
																group={getGroup(grp)}
																auth={auth}
															/>
															:
															<OlympicPlusGrid
																group={grp}
																auth={auth}
																stage={stage}
															/>
													}
												</div>
											)
										}
									</React.Fragment>
								)
							})
							:
							<>
								{auth && <NoContent table={'grids'} />}
							</>
					}
				</div>
			</div>

			<CombineTournamentMenu tab={'draws'} />
		</>
	)
}

export default memo(TournamentStandingComponent)