import './StageForm.scss'
import { memo, useState, useEffect, useRef, useCallback, useMemo } from 'react'
import SmoothCollapse from 'react-smooth-collapse'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { useParams, useNavigate } from 'react-router-dom'
import { DragDropContext } from 'react-beautiful-dnd'

import CombineTournamentMenu from '../../reusableComponents/CombineTournamentMenu/CombineTournamentMenu'
import HeaderTournament from '../../reusableComponents/HeaderTournament/HeaderTournament'
import { ReactComponent as ArrowDownIcon } from '../../icons/arrow_down.svg'
import StageGroup from '../StageGroup/StageGroup.jsx'
import { selectPlayersList, selectTournament, selectStage, selectChosenPlayerInList } from '../../redux/selectors.js'
import {
	useGetStageQuery,
	useRemoveStageMutation,
	useFillStageMutation
} from '../../redux/stageService/stageApiSlice.js'
import { getParametrs, checkEquality } from '../../utils/functions.js'
import {
	setPlayersList,
	setStageData,
	setDragState,
	dragParticipant,
	setChosenPlayerInList
} from '../../redux/stageService/stageSlice.js'
import RoundedButton from '../RoundedButton/RoundedButton'
import DoubleButton from '../DoubleButton/DoubleButton.jsx'
import Popup from '../../reusableComponents/Popup/Popup.jsx'
import Button from '../../reusableComponents/Button/Button.jsx'
import { TYPE_SINGLES, TYPE_DOUBLES, GROUP_TYPE_RR } from '../../utils/constants.js'

import {
	formNodes,
	formLevels,
	definePlacedPlayers,
	dragItems,
	cleanNodesInGroups,
	formNodesForRequest
} from './externalFunctions.js'

function StageFormComponent({tournamentName, tournamentStartDate, stagesCount, categories, tournamentRefetch}) {
	const { t } = useTranslation()
	const navigate = useNavigate()
	const dispatch = useDispatch()
	// ?
	const immutableLevels = useRef()
	//  
	const { levels } = useSelector(selectStage)
	// tournamentParams -  id 
	// stageNumber -   
	const { tournamentParams, stageNumber } = useParams()
	//   
	const [removeStage] = useRemoveStageMutation()
	//     
	const [fillStage] = useFillStageMutation()
	// useState     
	const [showMainMenu, setShowMainMenu] = useState(false)
	// useState         ( ?)
	const [removeStageConfirmation, setRemoveStageConfirmation] = useState(false)
	//  
	const playersList = useSelector(selectPlayersList)
	//  id 
	const { tournamentUid } = getParametrs(tournamentParams)
	// type -  ,   
	// doubles -    
	// players -    
	// stages_count -  
	const { type, doubles, players, stages_count } = useSelector(selectTournament)
	const [levelsMemory, setLevelsMemory] = useState(levels)
	const chosenPlayerInList = useSelector(selectChosenPlayerInList)

	//     
	const { data: stage = {}, isLoading } = useGetStageQuery({
		tournament_uid: tournamentUid,
		stageNumber
	}, { refetchOnMountOrArgChange: true })

	useEffect(() => {
		if (Object.keys(stage).length !== 0) {
			const { levels, rating_initial } = stage

			//  
			const formedLevels = formLevels(levels, players || doubles, type, stageNumber)
			//  
			const formedNodes = formNodes(rating_initial, players || doubles, type)

			dispatch(setPlayersList(formedNodes))
			const stageState = { ...stage, levels: formedLevels }
			dispatch(setStageData(stageState))
			immutableLevels.current = formedLevels
		}
	}, [stage, stageNumber, type, doubles, players, dispatch])

	useEffect(() => {
		if (levels?.length > 0) {
			const placedPlayers = definePlacedPlayers(levels, playersList)

			dispatch(setPlayersList(placedPlayers))
		}
	}, [levels, dispatch])

	const onDragEnd = useCallback((result) => {
		dispatch(setDragState({}))

		if (!result.destination) {
			return
		}

		const [endLevelIndex, endGroupIndex] = result.destination.droppableId.split('-')
		const { endIndex } = result.destination
		const replacedNode = { ...levels[endLevelIndex]?.groups[endGroupIndex]?.nodes[endIndex] }

		if (replacedNode?.disabled) {
			return
		}

		const obj = dragItems(result, levels)
		dispatch(dragParticipant(obj))
	}, [levels, dispatch])

	const isEquale = useMemo(() => {
		return checkEquality(immutableLevels.current, levels, type)
	}, [immutableLevels, levels, type])

	//   
	const cleanGroups = () => {
		const clearGroups = cleanNodesInGroups(levels)
		const stageState = { ...stage, levels: clearGroups }

		dispatch(setStageData(stageState))
	}

	//     
	const returnGroups = () => {
		const stageState = { ...stage, levels: levelsMemory }

		dispatch(setStageData(stageState))
	}

	//   
	const handleRemoveStage = useCallback(async () => {
		try {
			let response = {}

			for (let i = stageNumber - 1; i < stages_count; i++) {
				response = await removeStage({
					tournament_uid: tournamentUid
				}).unwrap()
		
				if (response?.error) {
					console.log(`Remove stage return error: ${response?.error}`)
				}
			}

			tournamentRefetch()

			const stageTabNumber = response?.order_number > 1 ? response?.order_number - 1 : 1

			dispatch(setPlayersList([]))
			dispatch(setChosenPlayerInList({}))
			setRemoveStageConfirmation(false)
		} catch (error) {
			console.log(`HandleRemoveStage stage catch error ${error}`)
		}
	}, [tournamentUid, dispatch, stageNumber, stages_count, removeStage, tournamentRefetch, navigate])


	const handleSaveFormedStage = useCallback(() => {
		try {
			const readyNodes = formNodesForRequest(stage, levels, type)

			const response = fillStage({
				tournament_uid: tournamentUid,
				stageNumber: stageNumber,
				body: readyNodes
			}).unwrap()
		
			if (response?.error) {
				console.log(`FormStage return error: ${response?.error}`)
			}
		} catch (error) {
			console.log(`HandleSaveFormedStage catch error: ${error}`)
		}
	}, [stage, levels, type, stageNumber, tournamentUid, fillStage])

	const handlePlayerInParticipantsList = useCallback((n, index) => {
		if (Object.keys(chosenPlayerInList)?.length === 0) {
			dispatch(setChosenPlayerInList({ player: n, index: index }))
		} else {
			dispatch(setChosenPlayerInList({}))
		}
	}, [dispatch, chosenPlayerInList])

	// console.log(playersList)
	// console.log(levels)

	return (
		<div className="stage-form">
			<HeaderTournament
				name={categories}
				date={tournamentStartDate}
				community={tournamentName}
			/>

			<div className="stage-form__wrapper">
				<div className="stage-form__header">
					<p className="stage-form__quantity">
						{ playersList?.length }
					</p>

					<p className="stage-form__stage-number">
						 { stage.order_number }

						{/* <button className="stage-creation__stage-button">
							<EditNoteIcon className="edit-note__icon" />
						</button> */}
					</p>

					<ArrowDownIcon
						className={`arrow-down__icon ${showMainMenu ? 'arrow--up' : 'arrow--down'}`}
						onClick={() => setShowMainMenu(prev => !prev)}
					/>
				</div>

				<div className="stage-form__list-participants">
					<SmoothCollapse expanded={showMainMenu}>
						<div className="stage-form__participants-list-wrapper">
							{
								playersList?.map((item, index) =>
									<div key={index}>
										{
											type === TYPE_SINGLES &&
												<div key={index}>
													{
														item?.playerUid ?
															<div
																className="stage-form__participants-list-container"
																key={index}
																style={{
																	color: !item?.placed ?
																		'var(--palette-live-blue)'
																		:
																		'var(--palette-live-grey-0)',
																	border: index === chosenPlayerInList.index ?
																		'solid #4694D9'
																		:
																		'solid var(--palette-live-grey-9)',
																	borderTopWidth: index === chosenPlayerInList.index ? 3 : 0,
																	borderBottomWidth: index === chosenPlayerInList.index ? 3 : 1,
																	borderRightWidth: index === chosenPlayerInList.index ? 3 : 0,
																	borderLeftWidth: index === chosenPlayerInList.index ? 3 : 0
																}}
																onClick={() => handlePlayerInParticipantsList(item, index)}
															>
																<p className="stage-form__participants-list-index">
																	{ index + 1 }
																</p>

																<div>
																	<p className="stage-form__participants-list-name">
																		{ item?.name }
																	</p>
																</div>

																<p className="stage-form__participants-list-rating">
																	[{ item?.rating }]
																</p>
															</div>
															:
															<p
																key={index}
																className="stage-form__participants-list-name"
																onClick={() => handlePlayerInParticipantsList(item, index)}
															>
																{ item?.name }
															</p>
													}
												</div>

										}

										{
											type === TYPE_DOUBLES &&
												<div key={index}>
													{
														item?.doubleUid ?
															<div
																className="stage-form__participants-list-container"
																key={index}
																style={{
																	color: !item?.placed ?
																		'var(--palette-live-blue)'
																		:
																		'var(--palette-live-grey-0)',
																	border: index === chosenPlayerInList.index ?
																		'solid #4694D9'
																		:
																		'solid var(--palette-live-grey-9)',
																	borderTopWidth: index === chosenPlayerInList.index ? 3 : 0,
																	borderBottomWidth: index === chosenPlayerInList.index ? 3 : 1,
																	borderRightWidth: index === chosenPlayerInList.index ? 3 : 0,
																	borderLeftWidth: index === chosenPlayerInList.index ? 3 : 0
																}}
																onClick={() => handlePlayerInParticipantsList(item, index)}
															>
																<p className="stage-form__participants-list-index">
																	{ index + 1 }
																</p>

																<div>
																	<p className="stage-form__participants-list-name">
																		{ item?.firstPlayerName }
																	</p>

																	<p className="stage-form__participants-list-name">
																		{ item?.secondPlayerName }
																	</p>
																</div>

																<p className="stage-form__participants-list-rating">
																	[{ item?.rating }]
																</p>
															</div>
															:
															<div
																className="stage-form__participants-list-container"
																key={index}
																style={{
																	color: !item?.placed ?
																		'var(--palette-live-blue)'
																		:
																		'var(--palette-live-grey-0)',
																	border: index === chosenPlayerInList.index ?
																		'solid #4694D9'
																		:
																		'solid var(--palette-live-grey-9)',
																	borderTopWidth: index === chosenPlayerInList.index ? 3 : 0,
																	borderBottomWidth: index === chosenPlayerInList.index ? 3 : 1,
																	borderRightWidth: index === chosenPlayerInList.index ? 3 : 0,
																	borderLeftWidth: index === chosenPlayerInList.index ? 3 : 0
																}}
																onClick={() => handlePlayerInParticipantsList(item, index)}
															>
																<div>
																	<p className="stage-form__participants-list-name">
																		{ item?.name }
																	</p>
																</div>
															</div>
													}
												</div>
										}
									</div>
								)
							}
						</div>
					</SmoothCollapse>
				</div>

				<DragDropContext
					onDragEnd={(result) => onDragEnd(result)}
					onDragUpdate={(result) => dispatch(setDragState(result))}
					onBeforeDragStart={(result) => dispatch(setDragState(result))}
				>
					{
						levels?.map((level, levelIndex) =>
							<div key={levelIndex} className="stage-form__level">
								{
									levels?.length > 1 &&
										<div className="stage-form__level-header">
											<p className="stage-form__level-name">
												{t('Level')} { level?.order_number }
											</p>
										</div>
								}

								<div className="stage-form__group-container">
									{
										level?.groups?.map((group, groupIndex) =>
											<div key={groupIndex} className="stage-form__group">
												<StageGroup
													levelIndex={levelIndex}
													groupIndex={groupIndex}
												/>
											</div>
										)
									}
								</div>
							</div>
						)
					}
				</DragDropContext>

				<RoundedButton
					title=""
					background="white"
					onClick={cleanGroups}
				/>

				<RoundedButton
					title=""
					background="white"
				/>

				{
					!isEquale &&
						<DoubleButton
							title=""
							onClickBig={() => handleSaveFormedStage()}
							onClickSmall={() => returnGroups()}
						/>
				}

				<button
					className="stage-form__delete-button"
					onClick={() => setRemoveStageConfirmation(true)}
				>
					{ t('Remove stage') }
				</button>

				{
					removeStageConfirmation &&
						<Popup title="   ">
							<p className="stage-form__confirmation">
								{t('Are you sure you want to remove stage?')}
							</p>

							<div className="stage-form__confirmation-buttons">
								<Button
									title={t('Cancel')}
									onClick={() => setRemoveStageConfirmation(false)}
									marginRight={true}
									marginBottom={false}
									background="white"
								/>

								<Button
									title={t('Remove')}
									onClick={handleRemoveStage}
								/>
							</div>
						</Popup>
				}
			</div>

			<CombineTournamentMenu />
		</div>
	)
}

export const StageForm = memo(StageFormComponent)






























