import { useSelector } from 'react-redux'
import { memo } from 'react'

import { StageForm } from '../../extendedComponents/StageForm/StageForm'
import { selectTournament } from '../../redux/selectors'
import StagePlug from '../../extendedComponents/StagePlug/StagePlug'

const TournamentStructureComponent = ({
	tournamentRefetch,
	tournamentName,
	tournamentStartDate,
	communityName,
	categories
}) => {
	const { stages_count } = useSelector(selectTournament)

	return (
		<>
			{
				stages_count > 0 ?
					<StageForm
						tournamentRefetch={tournamentRefetch}
						tournamentName={tournamentName}
						tournamentStartDate={tournamentStartDate}
						communityName={communityName}
						stagesCount={stages_count}
						categories={categories}
					/>
					:
					<StagePlug
						tournamentName={tournamentName}
						tournamentStartDate={tournamentStartDate}
						communityName={communityName}
					/>
			}
		</>
	)
}

export default memo(TournamentStructureComponent)