import { useLocation, useNavigate } from 'react-router-dom'
import { useEffect, useCallback } from 'react'

import { useGetCommunitiesQuery } from '../../redux/communitiesService/communitiesApiSlice'

import LocalStorageManager from '../../LocalStorageManager'
import Loader from '../../reusableComponents/Loader/Loader'

const Communities = () => {
	const navigate = useNavigate()
	const location = useLocation()
	const { lastCommunityUid } = LocalStorageManager.getStore()
	const { data = [], error, isLoading } = useGetCommunitiesQuery()

	const goToCommunity = useCallback((community) => {
		const queryParams = new URLSearchParams(location.search)
		const redirectPath = queryParams.get('redirect')

		LocalStorageManager.setItem('communityUid', community.uid)
		LocalStorageManager.setItem('communityName', community.name)

		navigate(redirectPath || '/tournaments')
	}, [navigate, location])

	useEffect(() => {
		if (data.length > 0 && !isLoading && !error) {
			LocalStorageManager.setItem('communities', data)

			let workCommunity

			if (lastCommunityUid) {
				workCommunity = data.find(community => community.uid === lastCommunityUid)
				if (!workCommunity) {
					workCommunity = data[0]
				}
			} else {
				workCommunity = data[0]
			}

			goToCommunity(workCommunity)
		}
	}, [data, isLoading, error, goToCommunity, lastCommunityUid])

	if (error) { console.log(error) }

	return (
		<>
			{
				isLoading && <Loader />
			}
		</>
	)
}

export default Communities
