import { memo } from 'react'
import './RoundedButton.scss'

function RoundedButton({ title, background, onClick, type }) {
	const white = 'white'
	const grey = 'grey'
	const darkGreyColor = 'var(--palette-live-grey-0)'
	const whiteColor = 'var(--palette-live-bg-1-white-old)'
	const lightGreyColor = 'var(--palette-live-grey-8)'
	const border = '1px solid ' + darkGreyColor

	return (
		<button
			className="rounded-button"
			style={{
				backgroundColor: background === white ? whiteColor
					: background === grey ? lightGreyColor
						: darkGreyColor,
				color: background === white || background === grey ? darkGreyColor
					: whiteColor,
				border: background === white ? border : ''
			}}
			onClick={onClick}
			type={type}
		>
			{title}
		</button>
	)
}

export default memo(RoundedButton)













